/******************
 autoselect model
******************/
function dropDownSelect(selected) {
  $(".choose-model select").val(selected);
}

/******************
 notification bar
******************/
$('#notify').click(function (e) {
    e.stopPropagation();
});
$('.notifications').click(function (e) {
    if (!$('.notify').is($(e.target))) {
        $('#notify').prop("checked", false);
    }
});

